<template>
  <a-card title="JSON文件">
    <a-card title="读取JSON文件">
      <p>加载可能需要一段时间，请耐心等待。</p>
      <input type="file" @change="read_json" accept=".json" ref="json_input"/>
    </a-card>
    <a-card title="生成JSON文件">
      <a-button v-on:click="gen_json">
        生成JSON文件
      </a-button>
    </a-card>
  </a-card>
  <a-card title="主播信息">
    <a-form v-for="(item, index) in streamer_form_template"
            v-bind:key="streamer_form_template+item"
            :label-col="{span:4}">
      <a-form-item :label="streamer_form_template_cn[index]">
        <a-input v-model:value="item.value"></a-input>
      </a-form-item>
    </a-form>
    <a-form-item label="视频总数" :label-col="{span:4}">
      <a-input v-model:value="video_forms.length" :disabled="true"></a-input>
    </a-form-item>
  </a-card>
  <a-card title="视频列表">
    <a-button v-on:click="insert_one">在末尾添加一个视频表单</a-button>
    <a-button v-on:click="delete_one">在末尾删除一个视频表单</a-button>
    <a-card title="格式举例: ">
      <a-form :label-col="{span: 4}"
              v-for="(_, index) in video_form_template"
              v-bind:key="video_form_template+index">
        <a-form-item :label="video_form_template_cn[index]">
          <a-input :disabled="true" v-model:value="video_form_demo[index]"></a-input>
        </a-form-item>
      </a-form>
    </a-card>
    <a-form v-for="(video_form, index) in video_forms" v-bind:key="video_form" :label-col="{span: 4}"
            v-model="video_forms">
      <a-divider v-if="!!video_form[0].value">视频 {{ index+1 }}: {{ video_form[0].value }}</a-divider>
      <a-divider v-else>视频 {{ index+1 }}</a-divider>
      <a-form v-for="(item, index) in video_form" v-bind:key="video_form+item" :label-col="{span: 4}">
        <a-form-item :label="video_form_template_cn[index]">
          <a-input v-model:value="item.value"></a-input>
        </a-form-item>
      </a-form>
    </a-form>
  </a-card>
</template>

<script>
import {saveAs} from 'file-saver'

export default {
  data() {
    return {
      video_forms: [],
      video_form_template: [
        {name: "video_title", value: ""},
        {name: "video_length", value: ""},
        {name: "video_publish_year", value: ""},
        {name: "video_publish_month", value: ""},
        {name: "video_publish_day", value: ""},
        {name: "video_publish_hour", value: ""},
        {name: "video_url", value: ""},
        {name: "cover_url", value: ""},
        {name: "extra_info", value: ""},
      ],
      json_video_template: {
        "video_title": "",
        "video_length": "",
        "video_publish_year": "",
        "video_publish_month": "",
        "video_publish_day": "",
        "video_publish_hour": "",
        "video_url": "",
        "cover_url": "",
        "extra_info": "",
      },
      video_form_template_cn: [
          "视频标题",
          "视频长度",
          "视频发布日期-年",
          "视频发布日期-月",
          "视频发布日期-日",
          "视频发布日期-小时",
          "视频url",
          "封面url",
          "补充信息"
      ],
      json_template: {
        "liver_name": "",
        "liver_avatar_url": "",
        "extra_info": "",
        "total_num": "",
        "video_list": []
      },
      video_form_demo: [
        "可爱黑猫星奈铃的初放送！",
        "01:01:47",
        "2021",
        "5",
        "9",
        "18",
        "www.bilibili.com/video/BV1z64y1d7Mp",
        "//i2.hdslb.com/bfs/archive/891d9d251e46275f1e4c5c36fa126db8901122d8.jpg@640w_400h_1c.webp",
        ""
      ],
      streamer_form_template: [
        {name: "liver_name", value: ""},
        {name: "liver_avatar_url", value: ""},
        {name: "extra_info", value: ""},
//        {name: "total_num"}
      ],
      streamer_form_template_cn: [
          "主播名称",
          "主播头像url",
          "介绍"
      ]
    }
  },
  methods: {
    insert_one() {
      let new_video_form = JSON.parse(JSON.stringify(this.video_form_template))
      this.video_forms.push(new_video_form)
      console.log(this.video_forms)
    },
    delete_one() {
      this.video_forms.pop()
    },
    read_json() {
      let files = this.$refs.json_input.files
      if(!files){
        return
      }
      let file = files[0]
      let reader = new FileReader()
      reader.readAsText(file)
      reader.onerror = function () {
        alert("Failed")
      }
      let body = this
      reader.onloadend = function () {
        let raw_obj = JSON.parse(reader.result)
         body.streamer_form_template[0].value = raw_obj.liver_name
         body.streamer_form_template[1].value = raw_obj.liver_avatar_url
         body.streamer_form_template[2].value = raw_obj.extra_info
        raw_obj.video_list.sort(function (video0, video1){
          return !(video0.video_publish_year <= video1.video_publish_year &&
              video0.video_publish_month <= video1.video_publish_month &&
              video0.video_publish_day <= video1.video_publish_day &&
              video0.video_publish_hour <= video1.video_publish_hour)
        })
         for(let i = 0 ; i < raw_obj.video_list.length ; i++){
           let raw_video = raw_obj.video_list.at(i)
           let new_video_form = JSON.parse(JSON.stringify(body.video_form_template))
           new_video_form[0].value = raw_video.video_title
           new_video_form[1].value = raw_video.video_length
           new_video_form[2].value = raw_video.video_publish_year
           new_video_form[3].value = raw_video.video_publish_month
           new_video_form[4].value = raw_video.video_publish_day
           new_video_form[5].value = raw_video.video_publish_hour
           new_video_form[6].value = raw_video.video_url
           new_video_form[7].value = raw_video.cover_url
           new_video_form[8].value = raw_video.extra_info
           body.video_forms.push(new_video_form)
         }
      }
    },
    gen_json() {
      let raw_obj = JSON.parse(JSON.stringify(this.json_template))
      raw_obj.liver_name = this.streamer_form_template[0].value
      raw_obj.liver_avatar_url = this.streamer_form_template[1].value
      raw_obj.extra_info = this.streamer_form_template[2].value
      raw_obj.total_num = this.video_forms.length.toString()
      for(let i = 0 ; i < this.video_forms.length ; i++){
        let raw_video = JSON.parse(JSON.stringify(this.json_video_template))
        raw_video.video_title = this.video_forms[i][0].value
        raw_video.video_length = this.video_forms[i][1].value
        raw_video.video_publish_year = this.video_forms[i][2].value
        raw_video.video_publish_month = this.video_forms[i][3].value
        raw_video.video_publish_day = this.video_forms[i][4].value
        raw_video.video_publish_hour = this.video_forms[i][5].value
        raw_video.video_url = this.video_forms[i][6].value
        raw_video.cover_url = this.video_forms[i][7].value
        raw_video.extra_info = this.video_forms[i][8].value
        raw_obj.video_list.push(raw_video)
      }
      let json = JSON.stringify(raw_obj)
      let blob = new Blob([json])
      saveAs(blob, "video-"+this.streamer_form_template[0].value+".json")
    }
  }
}
</script>

<style>
.ant-btn {
  margin: 5px;
}
</style>
